/*
-----------------------------------------------------------------------------
PAGE LAYOUT
-----------------------------------------------------------------------------*/
html{
	background-color: #fff;
}

a, input{
	outline: none;
}

a{
    color: #3a6a9a;
}

body{
	margin: 0px;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Noto Sans", Ubuntu, "Droid Sans", Arial, "Helvetica Neue", sans-serif;
	font-size: 14px;
	color: #3f3f3f;
	text-align: left;
}

.main-layout{
	display: grid;

	grid-template-areas:
	"header header"
	"nav content"
	"nav-action footer";

	grid-template-columns: 260px 1fr;
	grid-template-rows: auto 1fr auto;
	grid-gap: 0px;

	height: 100vh;
}

.main-layout header{
	grid-area: header;
	height: 66px;

	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 1px solid #d9d9d9;
}

.main-layout footer{
	grid-area: footer;
	height: 68px;

	padding: 0 20px;
	display: flex;
	align-items: center;
}

.main-layout footer a{
	margin-right: 20px;
	font-size: 13px;
	color: #3f3f3f;
	opacity: 0.6;
	text-decoration: none;
}

.main-layout footer a:hover{
	color: #47b6a1;
}

.main-layout main{
	grid-area: content;
}

.main-layout main .padded{
	padding: 20px 40px 60px 40px;
}

.main-layout nav{
	grid-area: nav;
	background-color: #f9fafa;
	border-right: 1px solid #efecec;
}

.main-layout nav .navigation-inner{
	padding-left: 20px;
	padding-right: 20px;
	padding-top: 10px;
}

.main-layout .nav-action{
	grid-area: nav-action;
	background-color: #f9fafa;
	border-right: 1px solid #efecec;
}

.main-layout .nav-action > div{
	height: 68px;
	border-top: 1px solid #efecec;
	margin: 0 20px;
	display: flex;
	align-items: center;
	justify-content: center;
}

select, input, button, textarea, button{
	font: inherit;
}

th,td{
	border: 0px;
	padding: 0px;
}

table{
	border-collapse: separate;
}

.clear:after {
    content: "";
    display: table;
    clear: both;
}

/*
-----------------------------------------------------------------------------
CUSTOMIZE YUI GRID
-----------------------------------------------------------------------------*/
.yui-g-row{
	padding: 5px 0px;
}

.yui-g-row-odd{
	background-color: #F8F8F8;
}

/*
-----------------------------------------------------------------------------
WORK AROUND HIGHCHARTS/YUI BUG
-----------------------------------------------------------------------------*/
.highcharts-tooltip text{
	line-height: 120%;
}

/*
-----------------------------------------------------------------------------
BLANK BACKGROUND
-----------------------------------------------------------------------------*/
html.blank body, html.blank{
	background-image: none !important;
}

/*
-----------------------------------------------------------------------------
LOGIN
-----------------------------------------------------------------------------*/
html.loginscreen body{
