}

html.loginscreen #auth-box{
	position: relative;
	margin: 0 auto 0 auto;
	width: 620px;
	min-height: 300px;
	padding-bottom: 10px;
	background-color: #fff;

	border-radius: 4px;
	overflow: hidden;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Noto Sans", Ubuntu, "Droid Sans", Arial, "Helvetica Neue", sans-serif;
}

html.loginscreen #auth-box .title{
