}

html.loginscreen #auth-box .auth-body .input-wrap{
	position: relative;
	margin-top: 20px;
	width: 100%;
}

html.loginscreen #auth-box .auth-body input[type=text],
html.loginscreen #auth-box .auth-body input[type=password],
html.loginscreen #auth-box .auth-body input[type=email]{
	border: 1px solid #d4d7d9;
	border-radius: 4px;
	padding: 15px 15px;
	width: 510px;

	background: #fff;

	font-size: 18px;
	font-weight: 700;
	color: #44627e;
}

html.loginscreen #auth-box .auth-body input[type=text]::placeholder,
html.loginscreen #auth-box .auth-body input[type=password]::placeholder,
html.loginscreen #auth-box .auth-body input[type=email]::placeholder {
