input[type=checkbox] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  display: inline-block;
  height: 16px;
  width: 16px;
  vertical-align: middle;
  border-width: 1px;
  border-radius: 0.25rem;
  background-color: #fff;
  border-color: #e2dfdf;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

input[type=checkbox]:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(66, 153, 225, 0.5);
}

input[type=checkbox]:focus:not(:checked) {
  border-color: #63b3ed;
}

input[type=checkbox]:checked {
  background-color: #3491df;
  border-color: transparent;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 16 16' fill='%23fff' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13.293 4.293a1 1 0 0 1 0 1.414L7 12a1 1 0 0 1-1.414 0L3.293 9.707a1 1 0 0 1 1.414-1.414l1.586 1.586 5.586-5.586a1 1 0 0 1 1.414 0z'/%3E%3C/svg%3E");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.switch {
  position: relative;
  display: inline-block;
  width: 80px !important;
  margin-top: 1px;
  height: 26px;
  background-color: rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  transition: all 0.3s;
  cursor: pointer;
}
.switch::after {
  content: '';
  position: absolute;
  width: 24px;
  height: 24px;
  border-radius:50%;
  background-color: white;
  top: 1px;
  left: 1px;
  transition: all 0.3s;
}

.checkbox:checked + .switch::after {
  left : 55px;
}
.checkbox:checked + .switch {
  background-color: #69c6b7;
}
.checkbox {
  display : none !important;
}

input[type=radio] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  display: inline-block;
  height: 1em;
  width: 1em;
